const GlobalStyle = createGlobalStyle `
  .custom-modal-transition-show {
    opacity: 0;
  
    > * {
      transform: scale(.75);
    }
  }

  .custom-modal-transition-show-active {
    opacity: 1;
    transition: 300ms ease-in-out;

    > * {
      transform: scale(1);
      transition: 300ms ease-in-out;
    }
  }

  .custom-modal-transition-hide {
    opacity: 1;
  
    > * {
      transform: scale(1);
    }
  }

  .custom-modal-transition-hide-active {
    opacity: 0;
    transition: 300ms ease-in-out;

    > * {
      transform: scale(.75);
      transition: 300ms ease-in-out;
    }
  }
`;
